import React from "react";
import { Route, Switch } from "react-router-dom";

import asyncComponent from "util/asyncComponent";

const App = ({ match }) => (
  <div className="gx-main-content-wrapper">
    <Switch>
      <Route path={`${match.url}dashboard`} component={asyncComponent(() => import('./Dashboard'))} />

      <Route path={`${match.url}barbers`} component={asyncComponent(() => import('./Barbers'))} />
      <Route path={`${match.url}barber_details/:barber_id`} component={asyncComponent(() => import('./BarberDetails'))} />

      <Route path={`${match.url}customers`} component={asyncComponent(() => import('./Customers'))} />

      <Route path={`${match.url}categories`} component={asyncComponent(() => import('./Categories'))} />
      <Route path={`${match.url}sub_categories`} component={asyncComponent(() => import('./SubCategories'))} />
      <Route path={`${match.url}services`} component={asyncComponent(() => import('./Services'))} />

      <Route path={`${match.url}booking_history`} component={asyncComponent(() => import('./BookingHistory'))} />
      <Route path={`${match.url}earning_reports`} component={asyncComponent(() => import('./EarningReports'))} />
      <Route path={`${match.url}barber_earning_history`} component={asyncComponent(() => import('./BarberEarningHistory'))} />

      <Route path={`${match.url}payment_request_details`} component={asyncComponent(() => import('./PaymentRequestedDetails'))} />
    </Switch>
  </div>
);

export default App;
