import React from "react";
import { Menu } from "antd";
import { Link } from "react-router-dom";

import CustomScrollbars from "util/CustomScrollbars";
import SidebarLogo from "./SidebarLogo";
import UserProfile from "./UserProfile";
import { NAV_STYLE_NO_HEADER_EXPANDED_SIDEBAR, NAV_STYLE_NO_HEADER_MINI_SIDEBAR, THEME_TYPE_LITE } from "../../constants/ThemeSetting";
import { useSelector } from "react-redux";

const SubMenu = Menu.SubMenu;

const SidebarContent = ({ sidebarCollapsed, setSidebarCollapsed }) => {

  let { navStyle, themeType } = useSelector(({ settings }) => settings);
  let { pathname } = useSelector(({ common }) => common);

  const getNoHeaderClass = (navStyle) => {
    if (navStyle === NAV_STYLE_NO_HEADER_MINI_SIDEBAR || navStyle === NAV_STYLE_NO_HEADER_EXPANDED_SIDEBAR) {
      return "gx-no-header-notifications";
    }
    return "";
  };

  const getNavStyleSubMenuClass = (navStyle) => {
    if (navStyle === NAV_STYLE_NO_HEADER_MINI_SIDEBAR) {
      return "gx-no-header-submenu-popup";
    }
    return "";
  };

  const selectedKeys = pathname.substr(1);
  const defaultOpenKeys = selectedKeys.split('/')[1];
  return (
    <>
      <SidebarLogo sidebarCollapsed={sidebarCollapsed} setSidebarCollapsed={setSidebarCollapsed} />
      <div className="gx-sidebar-content">
        <div className={`gx-sidebar-notifications ${getNoHeaderClass(navStyle)}`}>
          <UserProfile />
        </div>
        <CustomScrollbars className="gx-layout-sider-scrollbar">
          <Menu
            defaultOpenKeys={[defaultOpenKeys]}
            selectedKeys={[selectedKeys]}
            theme={themeType === THEME_TYPE_LITE ? 'lite' : 'dark'}
            mode="inline">

            <Menu.Item key="dashboard"> <Link to="/dashboard"> <i className="icon icon-dasbhoard" /><span>Dashboard</span></Link> </Menu.Item>
            <Menu.Item key="barbers"> <Link to="/barbers"> <i className="icon icon-profile" /><span>Barbers</span></Link> </Menu.Item>
            <Menu.Item key="customers"> <Link to="/customers"> <i className="icon icon-profile" /><span>Customers</span></Link> </Menu.Item>


            <Menu.Item key="categories"> <Link to="/categories"> <i className="icon icon-table-data" /><span>Categories</span></Link> </Menu.Item>
            <Menu.Item key="sub_categories"> <Link to="/sub_categories"> <i className="icon icon-table-data" /><span>Sub Categories</span></Link> </Menu.Item>
            <Menu.Item key="services"> <Link to="/services"> <i className="icon icon-table-data" /><span>Services</span></Link> </Menu.Item>

            <Menu.Item key="payment_request_details"> <Link to="/payment_request_details"> <i className="icon icon-table-data" /><span>Payment Requests</span></Link> </Menu.Item>

            <Menu.Item key="booking_history"> <Link to="/booking_history"> <i className="icon icon-table-data" /><span>Booking History</span></Link> </Menu.Item>
            <Menu.Item key="earning_reports"> <Link to="/earning_reports"> <i className="icon icon-pricing-table" /><span>Earning Reports</span></Link> </Menu.Item>
            <Menu.Item key="barber_earning_history"> <Link to="/barber_earning_history"> <i className="icon icon-pricing-table" /><span>Barber Earning History</span></Link> </Menu.Item>
          </Menu>
        </CustomScrollbars>
      </div>
    </>
  );
};

SidebarContent.propTypes = {};
export default SidebarContent;

